<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Kurikulum
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataKurikulum.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-for="(item, i) in dataKurikulum.jenjang_id.length"
            :key="i"
            v-model="dataKurikulum.jenjang_id[i]"
            :items="dataJenjang"
            item-text="nama"
            item-value="id"
            label="Pilihan jenjang"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
            append-outer-icon="mdi-minus"
            @click:append-outer="minus(i)"
          ></v-select>
          <v-btn
            depressed
            color="success"
            block
            @click="dataKurikulum.jenjang_id.push('')"
            class="mb-6">
            tambah jenjang
          </v-btn>

          <Notif :status="status" :msg="msg" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataKurikulum: {
          nama: '',
          jenjang_id: ['']
        },
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambahkan',
          error: 'Data gagal ditambahkan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataKurikulum', ['isLoading', 'status']),
      ...mapState('dataJenjang', ['dataJenjang'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          // console.log(this.dataKurikulum)
          this.$store.dispatch('dataKurikulum/postDataKurikulum', this.dataKurikulum)
          this.$store.dispatch('dataKurikulum/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      },
      minus(val) {
        this.dataKurikulum.jenjang_id.splice(val, 1)
      }
    }
  }
</script>
