<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataKurikulum"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataKurikulum.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditKurikulum :data="item" />
				<HapusKurikulum :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditKurikulum from './EditKurikulum'
	import HapusKurikulum from './HapusKurikulum'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				{ text: 'Jenjang', value: 'jenjang', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],
		}),

		components: {
			Loader,
			HapusKurikulum,
			EditKurikulum
		},

		created() {
			this.$store.dispatch('dataKurikulum/getDataKurikulum')
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataKurikulum/updateIsLoading', true)
		},

		computed: {
			...mapState('dataKurikulum', ['dataKurikulum', 'isLoading'])
		}
	}
</script>